.card {
    /*margin-top: 6rem;*/
}
.card .row{
    display: flex;
    align-items: center;
    gap:2rem;
    flex-wrap: wrap;
    padding: 2rem 0;
    padding-bottom: 3rem;

}

.card .price{
    font-size: 2rem;
    color:var(--pink);
    font-weight: bolder;
}

.card .heart {
    font-size: 2.5rem;
    color: #312921;
    margin-left: 1.5rem;
}

.card .heart:hover{
    color:var(--pink);
}

.card .row .img-container{
    flex: 1 1 40rem;

}

.card .row .img-container img{
    width:100%;
    border: 1.5rem solid #fff;
    border-radius: .5rem;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
    height: 100%;
    object-fit: cover;

}

.card .row .content{
    flex:1 1 40rem;
}

.card .row .content h3{
    font-size: 3rem;
    color:#312921;
}

.card .row .content p{
    font-size: 1.5rem;
    color: #312921;
    padding:.5rem 0;
    padding-top: 1rem;
    line-height: 1.5;
    margin-top: 2rem;
}