@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates:ital,wght@0,300;0,400;0,500;0,700;1,400;1,500;1,700&family=Nunito:wght@200&display=swap);
:root{
  --pink: #873c1e;
}
*{
  margin:0; padding:0;
  box-sizing: border-box;
  font-family: 'Montserrat Alternates', sans-serif;
  font-family: 'Nunito', sans-serif;
  outline: none; border:none;
  text-decoration: none;
  text-transform: none;
  text-transform: initial;
  transition: .2s linear;
}
html{
  font-size: 62.5%;
  scroll-behavior: smooth;
  scroll-padding-top: 6rem;
  overflow-x: hidden;
}
.home, .products, .form-auth, .card {
  padding:2rem 9%;
}
.heading{
  text-align: center;
  font-size: 3rem;
  color: #312921;
  padding:1rem;
  margin-top: 8rem;
  margin-bottom: 2rem;
  background-color: #ccaf9b;
}

.heading span{
  color:#873c1e;
  color:var(--pink);
}

.btn{
  display: inline-block;
  margin-top: 1rem;
  border-radius: 5rem;
  background: #312921;
  color:#fff;
  padding:.9rem 3.5rem;
  cursor: pointer;
  font-size: 1.7rem;
}

.btn:hover{
  background:#873c1e;
  background:var(--pink);
}

@media (max-width:991px){

  html {
    font-size: 55%;
  }

  .header {
    padding:2rem;
  }

  .home, .products, .footer, .form-auth, .card {
    padding:2rem;
  }

  .home{
    background-position: left;
  }
}

@media (max-width:768px){

  .home .content h3{
    font-size: 5rem;
  }

  .home .content span{
    font-size: 2.5rem;
  }
}

@media (max-width:450px){

  html{
    font-size: 50%;
  }

  .heading{
    font-size: 3rem;
  }
}




header{
    position: fixed;
    top:0; left:0; right:0;
    background:#fff;
    padding:2rem 9%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 1000;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
}

header .logo{
    font-size: 3rem;
    color: #312921;
    font-weight: bolder;
}
header .logo span{
    color:var(--pink);
}

header .navbar a{
    font-size: 2rem;
    padding:0 1.5rem;
    color:#666;
}

header .navbar a:hover{
    color:var(--pink);
}
header .icons a{
    font-size: 2.5rem;
    color: #873c1e;
    margin-left: 1.5rem;
}

header .icons a:hover{
    color: #ccaf9b;
}

@media (max-width:768px) {

    header .navbar {
        position: absolute;
        top: 100%;
        left: 0;
        right: 0;
        background: #eee;
        border-top: .1rem solid rgba(0, 0, 0, .1);
        -webkit-clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
                clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
    }

    header .navbar a {
        margin: 1.5rem;
        padding: 1.5rem;
        background: #fff;
        border: .1rem solid rgba(0, 0, 0, .1);
        display: block;
    }
}

.products .box-container{
    display: flex;
    flex-wrap: wrap;
    grid-gap:1.5rem;
    gap:1.5rem;
}
.products .box-container .box{
    flex:1 1 30rem;
    box-shadow: 0 .5rem 1.5rem rgba(0,0,0,.1);
    border-radius: .5rem;
    border:.1rem solid rgba(0,0,0,.1);
    position: relative;
}
.products .box-container .box .image{
    position: relative;
    text-align: center;
    padding-top: 2rem;
    overflow:hidden;
}
.products .box-container .box .image img{
    height:25rem;
}

.products .box-container .box:hover .image img{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
}

.products .box-container .box .image .icons{
    position: absolute;
    bottom:-7rem; left:0; right:0;
    display: flex;
}

.products .box-container .box:hover .image .icons{
    bottom:0;
}

.products .box-container .box .image .icons a{
    height: 5rem;
    line-height: 5rem;
    font-size: 2rem;
    width:50%;
    background:#ccaf9b;
    color:#fff;
}

.products .box-container .box .image .icons a:hover{
    background: #c6c3b3;
}

.products .box-container .box .content{
    padding:2rem;
    text-align: center;
}

.products .box-container .box .content h3{
    font-size: 2.5rem;
    color:#3a5b61;
}

.products .box-container .box .content .price{
    font-size: 2rem;
    color:var(--pink);
    font-weight: bolder;
    padding-top: 1rem;
}

.products .box-container .box .content span{
    font-size: 1.5rem;
    padding-top: 0.7rem;
    color: #bbb8b8;
    font-weight: lighter;
}



.form-auth {
    margin-top: 6rem;
}

.form-auth .row{
    display: flex;
    flex-wrap: wrap-reverse;
    grid-gap:1.5rem;
    gap:1.5rem;
    align-items: center;
}

.form-auth .row form{
    flex:1 1 40rem;
    padding:2rem 2.5rem;
    box-shadow: 0 .5rem 1.5rem rgba(0,0,0,.1);
    border:.1rem solid rgba(0,0,0,.1);
    background: #fff;
    border-radius: .5rem;
}

.form-auth .row .image{

    flex:1 1 40rem;
}

.form-auth .row .image img{
    width: 82%;
    margin-left: 4.5rem;
}

.form-auth .row form .box{
    padding:1rem;
    font-size: 1.7rem;
    color: #873c1e;
    text-transform: none;
    border:.1rem solid rgba(0,0,0,.1);
    border-radius: .5rem;
    margin:.7rem 0;
    width: 100%;
}

.form-auth .row form .box:focus{
    border-color: var(--pink);
}

.card {
    /*margin-top: 6rem;*/
}
.card .row{
    display: flex;
    align-items: center;
    grid-gap:2rem;
    gap:2rem;
    flex-wrap: wrap;
    padding: 2rem 0;
    padding-bottom: 3rem;

}

.card .price{
    font-size: 2rem;
    color:var(--pink);
    font-weight: bolder;
}

.card .heart {
    font-size: 2.5rem;
    color: #312921;
    margin-left: 1.5rem;
}

.card .heart:hover{
    color:var(--pink);
}

.card .row .img-container{
    flex: 1 1 40rem;

}

.card .row .img-container img{
    width:100%;
    border: 1.5rem solid #fff;
    border-radius: .5rem;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
    height: 100%;
    object-fit: cover;

}

.card .row .content{
    flex:1 1 40rem;
}

.card .row .content h3{
    font-size: 3rem;
    color:#312921;
}

.card .row .content p{
    font-size: 1.5rem;
    color: #312921;
    padding:.5rem 0;
    padding-top: 1rem;
    line-height: 1.5;
    margin-top: 2rem;
}
